import React, { useRef, useEffect } from "react"


export default function Contacto(props) {

    const sectionRef = useRef(null);

    /*
       El siguiente bloque de c√≥digo se encarga de las animaciones
       de aparici√≥n y desaparici√≥n de los elementos en pantalla
       seg√∫n el scroll del usuario
   
   */
    useEffect(() => {

        const observer = new IntersectionObserver((entries) => {
            entries.forEach((entry) => {
                entry.target.classList.toggle("hide-right", !entry.isIntersecting);
            });
        }, { threshold: 0.25 });

        observer.observe(sectionRef.current);

    }, []);


 

    return (
        <section className="contacto section" id="contacto-section">
            <h2 className="contacto--title">¬°Cont&aacute;ctame!</h2>
            
                <form ref={sectionRef} action="https://formsubmit.co/thegreencheesewheel@gmail.com" method="POST" className="contacto--form">
                    <input type="text" placeholder="Nombre" name="name" className="contacto--input"></input>
                    <input type="email" placeholder="e-mail" name="email" className="contacto--input"></input>
                    <input type="text" placeholder="Asunto" name="about" className="contacto--input"></input>
                    <textarea placeholder="Mensaje" name="message" className="contacto--input msg"></textarea>
                    <input type="submit" className="contacto--input contacto--submit"></input>
                </form>
                <svg id="visual" viewBox="0 0 900 600" width="900" height="600" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" version="1.1"><g transform="translate(447.7967358781548 290.5177827723595)"><path id="blob-1" d="M111 -128C142.3 -79.7 165.2 -39.8 176 10.8C186.9 61.5 185.7 123 154.4 173C123 223 61.5 261.5 13.1 248.4C-35.4 235.4 -70.7 170.7 -106 120.7C-141.4 70.7 -176.7 35.4 -177.8 -1.1C-178.8 -37.5 -145.6 -75 -110.3 -123.3C-75 -171.6 -37.5 -230.8 1.2 -232C39.8 -233.2 79.7 -176.3 111 -128" fill="#888277"></path></g> <g transform="translate(459.4828986378156 340.03208433619744)"><path id="blob-2" d="M145.8 -149.5C192.1 -99.5 235.1 -49.7 227.9 -7.2C220.7 35.4 163.4 70.7 117 102.4C70.7 134 35.4 162 -9.7 171.7C-54.7 181.3 -109.4 172.7 -156.9 141C-204.4 109.4 -244.7 54.7 -247.5 -2.8C-250.3 -60.3 -215.7 -120.7 -168.2 -170.7C-120.7 -220.7 -60.3 -260.3 -5.3 -255C49.7 -249.7 99.5 -199.5 145.8 -149.5" fill="#888277"></path></g> </svg>
            
        </section>
    );
}